/**
 * Breakpoints & Media Queries
 */
/**
 * Breakpoint mixins
 */
@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.nab-menu-searcher {
  margin: 1px;
}
.nab-menu-searcher input[type=text],
.nab-menu-searcher input[type=text]:focus {
  box-shadow: none;
}
.nab-menu-searcher > div[class*=control] {
  border-radius: 4px;
  border: 1px solid #7e8993;
  box-shadow: inset 0 1px 2px transparent;
  min-height: 0;
  padding: 0;
}
.nab-menu-searcher > div[class*=control] > div:first-child {
  padding-bottom: 0;
  padding-top: 0;
}
.nab-menu-searcher > div[class*=control]:nth-child(2) {
  border-color: #007cba;
  box-shadow: 0 0 0 1px #007cba;
}
.nab-menu-searcher [class*=control] > div > div:last-child:not([class*=indicatorContainer]) {
  margin: 0;
  height: 2em;
  padding: 0;
}
.nab-menu-searcher [class*=control] > div > div:last-child:not([class*=indicatorContainer]) input {
  height: 2em;
  line-height: 2;
  margin-top: -0.15em;
  margin-left: 0.15em;
}
.nab-menu-searcher [class*=indicatorContainer] {
  padding: 0 4px;
}
.nab-menu-searcher [class*=indicatorSeparator] {
  display: none;
}
.nab-menu-searcher [class*=menu] {
  border-radius: 0;
}